/// Gradient variables used in the original build
/// @group gradients
/// @type color
// Gradients are named by colour value and then mapped to purpose value.

@mixin linearGradient($startColor, $endColour) {
    background: linear-gradient(241.31deg, $endColour 0.52%, $startColor 49.67%);
}

@mixin gradientBlue() {
    @include linearGradient($color-primary, $color-primary-light);
}

@mixin gradientDarkBlue() {
    @include linearGradient($color-primary-dark, $color-primary);
}
